<template>
   <v-row justify="center">
    <v-dialog v-model="dialog" persistent  max-width="800px">
      <v-card>
        <v-card-title>
          <span class="headline"> <v-icon color="primary">mdi-calendar-multiple-check</v-icon> Ders Programı Ekle</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
                <v-col cols="12" sm="6">
      <v-date-picker v-model="date" range></v-date-picker>
    </v-col>
    <v-col cols="12" sm="6">
        <v-text-field
        :error-messages="errDate"
         persistent-hint v-model="dateRangeText" label="Seçilen Tarih" hint="Tarihi sol taraftaki takvimden seçiniz." prepend-icon="mdi-book-multiple" readonly></v-text-field>
    </v-col>
    <v-col cols="12">
        <el-upload
  class="upload-demo"
  drag
  :action="'#'"
  :on-change="fileUpload"
  :multiple="false"
  :limit="1"
  :auto-upload="false">
  <i class="el-icon-upload"></i>
  <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
  <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 5MB
    <p style="color:red"> {{errMsg}}</p>
  </div>
  <div style="color:red"></div>
</el-upload>

    </v-col>
            </v-row>
          </v-container>
        </v-card-text>
         <v-card-actions>
          <v-spacer></v-spacer>
           <v-btn  class="ma-2" @click="closeModal()" color="info" dark>{{$t('generate.close')}} </v-btn>
           <v-btn class="ma-2" :loading="loading" :disabled="loading" color="primary" @click="sendLesson()" dark>{{$t('generate.submit')}}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <snack-bar :snackbar="snackbar"  @finished="snackbar=$event"></snack-bar>
  </v-row>
</template>

<script>
export default {
props:{
      dialog:{
        default:false,
        type : Boolean,
        required:true
    },
},
computed:{
dateRangeText () {
        return this.date.join(' ~ ')
      },
},
watch:{
  date(data){
    this.lesson.startDate = data[0]
    this.lesson.endDate = data[1]
  },
},
methods:{
 async sendLesson(){
   this.errDate=[]
   this.loading = true
    if(this.lesson.startDate==''||this.lesson.endDate===undefined||this.lesson.endDate==''||this.lesson.file.uid===undefined){


      if(this.lesson.startDate==''){
        this.errDate.push('Başlangıç Tarihini Seçiniz')
      }
      if(this.lesson.endDate==''){
        this.errDate.push('Bitiş Tarihini Seçiniz')
      }
       if(this.lesson.endDate===undefined){
        this.errDate.push('Bitiş Tarihini Seçiniz')
      }
      if(this.lesson.file.uid===undefined){
        this.errMsg = 'Dosya seçiniz.'
      }
        this.loading = false
    }
    else{
      let fd = new FormData()
    fd.append('startDate',this.lesson.startDate)
    fd.append('endDate',this.lesson.endDate)
    fd.append('image',this.lesson.file)
    let result = await this.$api.postfd('lessonProgram','file',fd)
    if(result.status=='success'){
            this.$api.tnotify('Yeni Ders Programı Eklendi!',this.lesson.startDate+' ile '+ this.lesson.endDate + ' tarihleri arası Yeni Ders Programı Eklendi!','teachers')
            this.$api.tnotify('Yeni Ders Programı Eklendi!',this.lesson.startDate+' ile '+ this.lesson.endDate + ' tarihleri arası Yeni Ders Programı Eklendi!','parents')
      let type = 'jpg'
      if(this.lesson.file.type.includes('pdf'))
        type='pdf'
      let path = result.path.replace('public/',this.$api.BASE_API_URL)
      this.$emit('setNewFoodList',{startDate: this.lesson.startDate, endDate: this.lesson.endDate , id:result.id, file:path ,type:type})
         this.loading = false
         this.snackbar=true
      this.closeModal()
    }
    else{
         this.loading = false
      alert('hata')
    }
    }
  },
    fileUpload(e){
      if(e.raw.type==' application/pdf ' || e.raw.type=='image/jpeg' || e.raw.type=='image/png'){
          if(e.size>2000000){
       this.errMsg = "Dosya Boyutunuz 5MB üzerinde lütfen " +e.raw.type +' dosyanızı kaldırın. Dosya Boyutu : ' +e.raw.size+' bayt'
      }
      else{
        this.errMsg=""
       this.lesson.file = e.raw;
      }
      }
      else{
          this.errMsg="Dosya formatınız .pdf, .jpg, .jpeg veya .png olmalıdır."
      }
    },
    closeModal(){
        this.dialog=false
        this.$emit('dialog',false)
        this.lesson={
            startDate:"",
            endDate:"",
            file:[]
        }
        this.date=[]
    }
},
data(){
    return{
        date:[],
        loading:false,
        snackbar:false,
        errMsg:"",
        lesson:{
            startDate:"",
            endDate:"",
            file:[]
        },
        errDate:[]
    }
}
}
</script>

<style>
@media only screen and (max-width: 900px) {
  .v-dialog {
    margin : 0px !important
  }
}
.el-upload, .el-upload-dragger {
    width: 100% !important;
}
</style>