<template>
<v-container>
	<loading v-if="pageLoading"/>
	<div v-else>
		<div class="text-right my-4">

    <v-btn color="primary" @click="dialog=true">Ders Programı Ekle</v-btn>
</div>
<div class="bg-white">
      <h6 style="background:white;padding:10px" class="p-2 subtitle-2 font-weighh6t-light"> Veliler girilen tarihler arasındaki ders programlarını takip edebilirler.</h6>
</div>
<div id="gallery">
  <div @click="dialogImg=true;imgDetail={...lp,index:index}" v-for="(lp,index) in programs" :key="index"><iframe v-if="lp.type=='pdf'" class="p-0" :src="lp.file" width="40" frameborder="0"></iframe>
  <img v-else :src="lp.file"/>
  <a class="pp">{{lp.startDate}}- {{lp.endDate}}</a></div>
</div>
     <insert-lesson-dialogs @dialog="dialog=$event" @setNewFoodList="programs.unshift($event)"  :dialog="dialog"></insert-lesson-dialogs>
      <v-dialog v-model="dialogImg"  max-width="600px">
       <v-card v-if="imgDetail!=null">
		<v-card-title>
        <span class="headline"><v-icon class="mx-3" color="primary">mdi-calendar-multiple-check</v-icon>Ders Programı </span>
			<v-row justify="end" align="center">
			<v-tooltip class="mx-2 " bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="processData={id:imgDetail.id,index:imgDetail.index};dialog3=true;">
          <v-icon
        class="ml-2"
          color="danger"
          dark
          v-bind="attrs"
          v-on="on"
        >mdi-delete</v-icon>
        </span>
      </template>
      <span>Ders Programını Kaldır</span>
    </v-tooltip>
	<v-tooltip class="mx-2 " bottom>
      <template v-slot:activator="{ on, attrs }">
        <span @click="dialogImg=false">
          <v-icon
        class="ml-2"
          color="danger"
          dark
          v-bind="attrs"
          v-on="on"
        >mdi-close-box</v-icon>
        </span>
      </template>
      <span>Kapat</span>
    </v-tooltip>
		</v-row>
        </v-card-title>
         <img v-if="imgDetail.type=='jpg'" width="100%" :src="imgDetail.file" alt="">
         <iframe :src="imgDetail.file" v-else width="100%" frameborder="0"></iframe>
       </v-card>
     </v-dialog>
	<confirm :dialog="dialog3" @cancel="dialog3=false" @confirm="deleteItem" />
    <snack-bar :snackbar="snackbar"></snack-bar>
	</div>
</v-container>

</template>

<script>
import insertLessonDialogs from '../../components/dialogs/insertLessonDialogs'
export default {
components:{
    insertLessonDialogs
},
data(){
    return{
    dialog:false,
    dialog3:false,
    dialogImg:false,
	programs:[],
	pageLoading:false,
	processData:{},
	snackbar:false,
    imgDetail:null
    }
},
methods:{
	deleteItem(){
    this.$api.put('lessonProgram',this.processData.id, {isActive:0}).then(result=>{
    if(result.result==1){
        this.snackbar=true
        this.programs.splice(this.processData.index,1)
        this.dialog3 = false
		this.processData = null
		this.dialogImg=false
         setTimeout(() => {
           this.snackbar=false
         }, 4000);
      }
      else{
		this.dialogImg=false
        this.processData = null

      }
    })

  },
},
mounted(){
    this.$api.get('lessonProgram?limit=20').then(res=>{
        if(res.count!=0){
            res.body.forEach(pr=>{
				pr.file = pr.file.replace('public/',this.$api.BASE_API_URL)
                pr.startDate = pr.startDate.substring(0,10)
                pr.endDate = pr.endDate.substring(0,10)
                if(pr.file.split('').reverse().join('').split('.')[0].split('').reverse().join('')=='pdf'){
                    pr.type = 'pdf'
                }
                else{
                    pr.type = 'jpg'
                }
            })
			this.programs = res.body
			this.pageLoading=false
        }
        else{
            this.programs = []
        }
    })
}
}
</script>

<style scoped src="../../../src/assets/galery.css"></style>